.page-component{display:grid;grid-template-columns:150px calc(1100px + 4rem) auto}.page-component .card{padding:2rem;cursor:pointer}.page-component-form{position:relative;padding-left:2rem;padding-right:2rem;width:calc(100% - 2rem)}.page-component-form .card{position:absolute;right:-200%}.page-component-form.active{transform:translateX(0)}.page-component-form.active .card{right:-2rem}
